'use strict';

export default function openModalWindowDelete(taskIndex) {
    document.querySelector('#modalLabelDelete .modal-title').innerHTML = '';
    let modalBody = document.querySelector('#modalDelete .modal-body');

    modalBody.innerHTML = `<label class="checkbox-inline" data-task-index="${taskIndex}"><input type="checkbox" id="inlineCheckboxFileDelete">   ,     </label>`;

    $('#modalDelete').modal('show');
}


